<template>
  <br>
  <div>
    <h5>我在保單上勾選「否」，但我實際上罹患..</h5>
    <div v-for="(item, index) in diseaseList" :key="index" class="form-check form-check-inline form-control-lg">
        <input class="form-check-input" type="checkbox" :id="index" :value="item" v-model="disease">
        <label class="form-check-label" :for="index">{{item}}</label>
    </div>
    <br>
    <button @click="press" class="btn btn-outline-success" type="button">
        <b>確認</b>
    </button>
  </div>
</template>

<script>
export default {
  name: "9-1",
  data (){
    return {
      disease: [],
      diseaseList: ['高血壓', '糖尿病', '心肌梗塞', '支氣管炎', '粉瘤', '大腸瘜肉']
    }
  },
  methods: {
    press: function(){
      if(this.disease.length == 0){
        alert("請至少勾選一個選項");
      }
      else{
          this.$router.push({ 
            name: '10',
            params:{
                name: this.$route.params.name,
                type: this.$route.params.type,
                samePeople: this.$route.params.samePeople,
                insurerCareer: this.$route.params.insurerCareer,
                insuredCareer: this.$route.params.insuredCareer,
                insurerDate: this.$route.params.insurerDate,
                serviceDate: this.$route.params.serviceDate,
                disease: this.disease
            }
          });
      }
    }
  }
};
</script>

